.login {
  height: 100vh;
  position: relative;
}

.login_container {
  width: 25%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.login_buttons {
  flex-direction: column;
  display: flex;
}

.login_btn__main {
  margin-bottom: 22px;
}



.btn {
  background: none;
  border: none;
  outline: none;
}

.btn_text {
  border: 2px solid #767575;
  border-width: 2px 0;
  padding: 8px 0;
  display: inline-block;
}

.btn__main {
  border: 2px solid #000;
  border-radius: 6px;
  padding: 18px 27px;
}

